//   
import React, { useState, useEffect } from "react";
import {
  Button,
  TextField,
  Paper,
  Checkbox,
  FormControlLabel,
  Grid,
  Box
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Fade from "@material-ui/core/Fade";
import Swal from "sweetalert2";
import axios from "axios";

//    
const useStyles = makeStyles((theme) => ({
  paper: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: theme.spacing(2)
  },
  form: {
    width: "100%",
    marginTop: theme.spacing(1)
  },
  passwordField: {
    margin: theme.spacing(-4, 0, 2)
  },
  submit: {
    margin: theme.spacing(3, 0, 1)
  },
  buttonGroup: {
    marginTop: theme.spacing(1)
  },
  adminMargin: {
    marginBottom: theme.spacing(5)
  }
}));

// SignIn   
export default function SignIn() {
  //    
  const classes = useStyles();

  //   
  const [dong, setDong] = useState("");
  const [ho, setHo] = useState("");
  const [password, setPassword] = useState("");
  const [adminMode, setAdminMode] = useState(false);

  // 'dong'     
  const handleDongChange = (e) => {
    const { value } = e.target;

    //   
    if (/^[0-9]*$/.test(value)) {
      setDong(value);
    } else {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "''  .   ."
      });
    }

    //  SQL       
    if (/(--|;|'|"|=|OR|AND)/i.test(value)) {
      Swal.fire({
        icon: "error",
        title: "Security Alert...",
        text: "SQL     ."
      });
    }
  };

  // 'ho'     
  const handleHoChange = (e) => {
    const { value } = e.target;

    //   
    if (/^[0-9]*$/.test(value)) {
      setHo(value);
    } else {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "''  .   ."
      });
    }

    //  SQL       
    if (/(--|;|'|"|=|OR|AND)/i.test(value)) {
      Swal.fire({
        icon: "error",
        title: "Security Alert...",
        text: "SQL     ."
      });
    }
  };

  // 'password'     
  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  // form submit   
  const handleSubmit = (e) => {
    e.preventDefault();

    // , ,        
    if (!dong || !ho || !password) {
      Swal.fire({
        icon: "warning",
        title: "Oops...",
        text: "   !"
      });
    } else {
      const postData = {
        dong: dong,
        ho: ho,
        pw: password,
        isAdmin: adminMode ? "y" : "n"
      };

      axios
        .post("http://61.81.98.195:3000/", postData)
        .then((response) => {
          //     .
          console.log(response);
        })
        .catch((error) => {
          //     .
          console.error(error);
        });
    }
  };

  // 'adminMode'    effect hook
  useEffect(() => {
    if (ho === "0000") {
      setAdminMode(true);
    } else {
      setAdminMode(false);
    }
  }, [ho]);

  // SignIn  
  return (
    <Paper className={classes.paper}>
      <form className={classes.form} noValidate>
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="dong"
              label=""
              name="dong"
              type="text"
              value={dong}
              onChange={handleDongChange}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="ho"
              label=""
              name="ho"
              type="text"
              value={ho}
              onChange={handleHoChange}
            />
          </Grid>
        </Grid>
        <Fade in={adminMode}>
          <Box className={adminMode ? classes.adminMargin : null}>
            <FormControlLabel
              control={
                <Checkbox checked={adminMode} name="adminCheckbox" disabled />
              }
              label="Login Admin"
            />
          </Box>
        </Fade>

        <TextField
          variant="outlined"
          margin="none"
          required
          fullWidth
          name="password"
          label=""
          type="password"
          id="password"
          value={password}
          onChange={handlePasswordChange}
          className={classes.passwordField}
        />

        <Button
          type="submit"
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={handleSubmit}
        >
          
        </Button>
        <Grid container spacing={2} className={classes.buttonGroup}>
          <Grid item xs={6}>
            <Button fullWidth variant="contained" color="secondary">
               
            </Button>
          </Grid>
          <Grid item xs={6}>
            <Button fullWidth variant="contained" color="default">
              
            </Button>
          </Grid>
        </Grid>
      </form>
    </Paper>
  );
}
